import { create } from 'zustand'
import { WixClientContext } from '@/Context/WixContext/WixContext'

// const wixClent = WixClientContext();
export const useCartStore = create((set) => ({
    cart:[],
    isLoading:true,
    counter:0,
    getCart: async (wixClent)=>{
        const cart = await wixClent.currentCart.getCurrentCart();
        set({cart:(cart || []),isLoading:false,counter:cart?.lineItems.length || 0})
    },
    addItem: async (wixClient)=>{},
    removeItem: async (wixClient)=>{},
}))
